import { useState } from "react";
import { Button,Navbar,Offcanvas,Form,Container,Nav} from "react-bootstrap";
import { Link } from "react-router-dom";
import axios from "axios";
import qs from "qs";
import {useCookies} from "react-cookie";

function Header(){

    const [show, setShow] = useState(false);
    const [username, setUsername] = useState('');
    const [passwd, setPasswd] = useState('');
    const [cookies, setCookie] = useCookies(['id'])

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const onUsernameHandler = (e) =>{
        setUsername(e.target.value);
    }
    const onPasswdHandler = (e) =>{
        setPasswd(e.target.value);
    }
    const responsebody ={
        username:username,
        passwd:passwd,
    }

    const axiosConfig = {
        headers : {
            "Content-Type" : "application/x-www-form-urlencoded"}
    }

    const axiosBody = {
        username:username,
        passwd:passwd
    }



    const onSubmitHandler = (e) =>{
        e.preventDefault();

        console.log(e.target)
        console.log(username)
        console.log(passwd)
        console.log(axiosBody)
        axios.post("/member/login",
            qs.stringify(axiosBody),
            axiosConfig)
            .then((response) => {
                console.log(response.data);
                window.alert(response.data.msg);
                setCookie('id', response.data.msg);
            })
            .catch((error) => {
                alert(' .');
            });



        // fetch("/member/login", {
        //     headers : {"Content-Type":"x-www-form-urlencoded",
        //                },
        //     method: "POST",
        //     redirect: "follow",
        //     body: responsebody,
        // }).then(response => response.json())
        //     .then(data=>{
        //         console.log(data);
        //         window.alert(data.msg);
        //         // window.location.href =data.url;
        //     })
    }


  
    return (
        <Navbar bg="light" expand="lg" style={{height :'15vh', textAlign: 'center', display: 'flex', alignItems:'center',boxShadow: '1px 1px 3px 1px #44194C'}}>
            <Container fluid style={{justifyContent:'around' }}>
                <Navbar.Brand className="fw-bold" style={{color: 'black', fontSize: '1.5em' }} href="/">Music Recommandation</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Nav className="me-auto">
                    <Link to="/" style={{fontSize: '1.1em', color: 'black', textDecoration: 'none'}}>Home</Link>
                    <Link to="/emotion" style={{fontSize: '1.1em', color: 'black', textDecoration: 'none'}}>emotion</Link>
                </Nav>
            </Container>
            <Button variant="secondary" onClick={handleShow} className="m-1 mx-lg-4" >
                Login
            </Button>
            <Offcanvas show={show} onHide={handleClose} placement="end">
              <Offcanvas.Header closeButton>
                <Offcanvas.Title></Offcanvas.Title>
              </Offcanvas.Header>
              <Offcanvas.Body>
                <Form onSubmit={onSubmitHandler}>
                    <fieldset>
                        <Form.Group className="mb-3">
                            <Form.Label htmlFor="username"></Form.Label>
                            <Form.Control type ="text" onChange = {onUsernameHandler} id="username" value={username}  placeholder="" />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Label htmlFor="passwd"></Form.Label>
                            <Form.Control type = "password" onChange = {onPasswdHandler} id="passwd" value={passwd} placeholder="" />
                        </Form.Group>
                        <Form.Group className="mb-3">
                            <Form.Check
                                type="checkbox"
                                id="PwSave"
                                label=" "
                            />
                        </Form.Group>
                        <Form.Group className="d-flex flex-row gap-3 px-3 mt-lg-3" >
                            <Button variant="secondary" size='lg' type="submit"></Button>
                            <Link to='/register'><Button variant="secondary" size='lg' type="submit" ></Button></Link>
                            <Button variant="secondary" size='lg' type="submit"></Button>
                        </Form.Group>
                    </fieldset>
                </Form>
              </Offcanvas.Body>
            </Offcanvas>    
    </Navbar>);
}

export default Header